.social_media_card{
    background-color: #EEE5FF;
    background-image: url(../img/shape_2.svg);
    background-size: cover;
    background-position: top right;
    background-repeat: no-repeat;
    .media_card_body{
        padding: 25px;
        position: relative;
        z-index: 1;
        &::before{
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            background: #FFFFFF;
            width: 100%;
            bottom: 0;
            top: 70px;
            z-index: -1;
            border-radius: 15px;
        }
        .media_card_list{
            display: grid;
            grid-template-columns: repeat(2,1fr);
            grid-gap: 25px;
            .single_media_card{
                background: #FFF4DE;
                border-radius: 15px;
                height: 136px;
                display: flex;
                align-items: self-start;
                flex-direction: column;
                justify-content: end;
                padding: 25px;
                span{
                    color: #FFC881;
                    font-size: 12px;
                    font-weight: 700;
                }
                h3{
                    font-size: 28px;
                    font-weight: 900;
                    margin-bottom: 0;
                    color: #FFAD44;
                }
                &:nth-child(2){
                    background: #E2FFF6;
                    span{
                        color: #69E2BD;
                    }
                    h3{
                        color: #25C997;
                    }
                }
                &:nth-child(3){
                    background: #FFE2E5;
                    span{
                        color: #FF8895;
                    }
                    h3{
                        color: #F64E60;
                    }
                }
                &:nth-child(4){
                    background: #E1F0FF;
                    span{
                        color: #76B5F5;
                    }
                    h3{
                        color: #3699FF;
                    }
                }
            }
        }
    }
}


.sales_card_wrapper{
    background-color: #F65365;
    position: relative;
    background-image: url(../img/plane_thumb.svg);
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 100% 58%;
    background-position: top left;
    .sales_card_body{
        background: #fff;
        border-radius: 15px;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        row-gap: 40px;
        padding: 30px 70px;
        position: relative;
        margin: 200px 15px 0px 15px;
        bottom: 15px;
        .single_sales{
            span{
                font-size: 12px;
                font-weight: 700;
                color: #757575;
                display: block;
                margin-bottom: 5px;
            }
            h3{
                font-size: 25px;
                color: #474D58;
                font-weight: 900;
                margin-bottom: 0;
            }
        }
    }
}


