.common_bootstrap_button{
     span {
        background: #F64E60;
        padding: 7px 12px;
        text-transform: capitalize;
        display: inline-block;
        font-size: 14px;
        font-weight: 600;
        color: #fff;
        border-radius: 2px;
        cursor: pointer;
        min-height: 36px;
    } 
}
.page_date_button{
    text-transform: capitalize;
    color: #9995B6;
    border-radius: 5px;
    font-size: 11px;
    font-weight: 600;
    cursor: pointer;
    transition: .3s;
    min-height: 36px;
    img{
        margin-right: 10px;
    }
}
.page_title_right{
    display: flex;
    flex-wrap: wrap;
    .page_date_button{
        margin-right: 10px;
    }
}